import React from 'react'
import Gallery from './../shared/gallery';
import PageTitle from './../shared/page-title';
import SectionTitle from './../shared/section-title';
import Paragraph from './../shared/paragraph';
import PageIntro from '../shared/page-intro';
import StoreButtons from './../shared/store-buttons';
import Video from './../shared/video';
import Comments from './../shared/comments';

import logo from './../img/cubic-roll/logo.png';
import screenshot11 from './../img/cubic-roll/screenshot1.png';
import screenshot12 from './../img/cubic-roll/screenshot2.png';
import screenshot13 from './../img/cubic-roll/screenshot3.png';
import screenshot14 from './../img/cubic-roll/screenshot4.png';
import screenshot15 from './../img/cubic-roll/screenshot5.png';
import screenshot21 from './../img/cubic-roll/screenshots_1_1/1.png';
import screenshot22 from './../img/cubic-roll/screenshots_1_1/2.png';
import screenshot23 from './../img/cubic-roll/screenshots_1_1/3.png';
import screenshot24 from './../img/cubic-roll/screenshots_1_1/4.png';
import screenshot25 from './../img/cubic-roll/screenshots_1_1/5.png';

export default class Page extends React.Component {
    render() {
        return (
            <div>
                <PageTitle text="Cubic Roll"/>

                <PageIntro logo={logo}>
                    <Paragraph>Keep an accelerating rolling cube on a randomly generated track.</Paragraph>
                    <Paragraph><strong>Release date:</strong> v1.0 - June 26, 2014, v1.1 - October 5, 2014</Paragraph>
                    <Paragraph><strong>Platforms:</strong> PC, iOS</Paragraph>
                    <StoreButtons desura="http://desura.com/games/cubic-roll/"
                                  appStore="https://itunes.apple.com/us/app/cubic-roll/id926093609"
                                  itch="http://0xc0dec.itch.io/cubic-roll"/>
                    <Video src="//www.youtube.com/embed/LIk4Yl86uqw"/>
                </PageIntro>

                <SectionTitle text="Screenshots"/>

                <SectionTitle text="Version 1.1" small/>
                <Gallery
                    images={[
                        { src: screenshot21 },
                        { src: screenshot22 },
                        { src: screenshot23 },
                        { src: screenshot24 },
                        { src: screenshot25 }
                    ]}
                />

                <SectionTitle text="Version 1.0" small/>
                <Gallery
                    images={[
                        { src: screenshot11 },
                        { src: screenshot12 },
                        { src: screenshot13 },
                        { src: screenshot14 },
                        { src: screenshot15 }
                    ]}
                />

                <Comments/>
            </div>
        );
    }
}


